import React, { useState, useEffect } from 'react'
import { Col, Form, Modal } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';
import Loading from '../base/Loading';
import { ValidarNit } from '../../../helpers/ValidarNit';
const menuIdMunicipio = 10;
export const ProveedorUpSert = ({ dataInicial, abrirModal, setAbrirModal, GetProveedores, catDepartamento }) => {
    const state = useSelector(state => state);
    const [values, handleOnChange, , setValues] = useForm(dataInicial);
    const [loading, setLoading] = useState(false);
    const [accesos, setAccesos] = useState([]);
    const [departamentoId, setDepartamentoId] = useState('');
    const [readOnlyNombre, setReadOnlyNombre] = useState(false);
    const [catMunicipio, setCatMunicipio] = useState([]);

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuIdMunicipio);
            setAccesos(misAccesos);
        }
    }

    const NuevoRegistro = async () => {
        let response = await callApi('proveedor', {
            method: 'POST',
            body: JSON.stringify(values)
        });

        if (response) {
            alert_exitoso("Proveedor registrado exitosamente");
            GetProveedores();
            setAbrirModal(false);
        }
    }
    const ActualizarRegistro = async () => {
        let response = await callApi(`proveedor/${dataInicial.proveedorId}`, {
            method: 'PUT',
            body: JSON.stringify(values)
        });

        if (response) {
            alert_exitoso(response);
            GetProveedores();
        }
        setAbrirModal(false);
    }
    const onchangeDepartamento = ({ target }) => {
        setDepartamentoId(target.value)
    }
    const handleOnSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        if (dataInicial.proveedorId > 0) {
            await ActualizarRegistro();
        } else {
            await NuevoRegistro();
        }
        setLoading(false);
    }


    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la informacin solicitada por el formulario");
    };

    const GetMunicipio = async (id) => {
        if (id > 0) {
            if (accesos.find(acceso => acceso.menuId === menuIdMunicipio && acceso.accesoId === 3)) {
                setLoading(true);
                let response = await callApi(`municipio?departamentoId=${id}&estadoId=1&include=0`);
                if (response) {
                    setCatMunicipio(response);
                }
            } else {
                setCatMunicipio([{ municipioId: '', descripcion: 'No esta autorizado' }]);
            }
        }
        setLoading(false);
    }

    
    const GetInfoNit = async () => {
                setLoading(true);
                let response = await callApi(`sat/nit/${values.nit}?p=1`);
                if (response) {
                    setValues({
                        ...values,
                        nombre:response.nombre 
                    });
                    setReadOnlyNombre(true);
                }
        setLoading(false);
    }


    useEffect(() => {
        GetAccesosByMenuId();
    }, []);

    useEffect(() => {
        if (dataInicial.departamentoId) {
            setDepartamentoId(dataInicial.departamentoId);
        }
    }, [dataInicial.departamentoId]);

    useEffect(() => {
        GetMunicipio(departamentoId);
    }, [departamentoId, accesos]);

    useEffect(() => {
        if (values.nit.trim().length > 0) {
            let expresion=new RegExp("([0-9])[zZ]{0,1}$");
            if (expresion.test(values.nit)) {
                if (ValidarNit(values.nit)) {
                    GetInfoNit();
                };
            }else{
                setReadOnlyNombre(false);
            }
        }
    }, [values.nit]);

    const errorMessage = "Campo obligatorio";
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">{dataInicial.proveedorId > 0 ? 'Actualizar Proveedor' : 'Nuevo Proveedor'}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                                <Form.Row>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="nit">Nit</Form.Label>
                                        <TextInput
                                            name="nit"
                                            id="nit"
                                            required
                                            value={values.nit}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Nombre"
                                            autoComplete="off"
                                            type="text"
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="nombre">Nombre</Form.Label>
                                        <TextInput
                                            name="nombre"
                                            id="nombre"
                                            required
                                            value={values.nombre}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Nombre"
                                            autoComplete="off"
                                            type="text"
                                            readOnly={readOnlyNombre}
                                        />
                                    </Form.Group>



                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="descripcion">Descripcin</Form.Label>
                                        <TextInput
                                            name="descripcion"
                                            id="descripcion"
                                            required
                                            multiline
                                            value={values.descripcion}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Descripcin"
                                            autoComplete="off"
                                            type="text"
                                            rows="3"
                                            minLength="10"
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="departamentoId">Departamento</Form.Label>
                                        <SelectGroup
                                            name="departamentoId"
                                            id="departamentoId"
                                            required
                                            value={departamentoId}
                                            onChange={onchangeDepartamento}
                                            errorMessage={errorMessage}>
                                            <option value="">Seleccione un departamento</option>
                                            {
                                                catDepartamento.map(({ departamentoId, descripcion }) => (
                                                    <option value={departamentoId} key={departamentoId}>{descripcion}</option>
                                                )
                                                )
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="municipioId">Municipio</Form.Label>
                                        <SelectGroup
                                            name="municipioId"
                                            id="municipioId"
                                            required
                                            value={values.municipioId}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}>
                                            <option value="">Seleccione un municipio</option>
                                            {
                                                catMunicipio.map(({ municipioId, descripcion }) => (
                                                    <option value={municipioId} key={municipioId}>{descripcion}</option>
                                                ))
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="direccion">Direccion</Form.Label>
                                        <TextInput
                                            name="direccion"
                                            id="direccion"
                                            required
                                            multiline
                                            value={values.direccion}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Direccion"
                                            autoComplete="off"
                                            type="text"
                                            rows="3"
                                            minLength="10"
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="email">Correo</Form.Label>
                                        <TextInput
                                            name="email"
                                            id="email"
                                            required
                                            value={values.email}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Correo electrncio"
                                            autoComplete="off"
                                            type="email"
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="estadoId">Estado</Form.Label>
                                        <SelectGroup
                                            name="estadoId"
                                            id="estadoId"
                                            value={values.estadoId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">Seleccione un estado</option>
                                            <option value="1">Activo</option>
                                            <option value="2">Inactivo</option>
                                        </SelectGroup>
                                    </Form.Group>

                                    <div className="col-sm-4"></div>
                                    <div className="col-sm-2">
                                        <button type="button" onClick={() => { setAbrirModal(false) }} className="btn btn-warning"> Cancelar</button>
                                    </div>
                                    <div className="col-sm-2">
                                        <button type="submit" className="btn btn-success"> {dataInicial.proveedorId > 0 ? 'Actualizar' : 'Registrar'}</button>
                                    </div>
                                </Form.Row>
                            </ValidationForm>
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
