import React, { useEffect, useState } from 'react'
import { Row, Col, Card, Button, Table } from 'react-bootstrap';
import moment from 'moment';
import Swal from 'sweetalert2';
import { useSelector } from 'react-redux';
import withReactContent from 'sweetalert2-react-content';
import callApi from '../../../helpers/conectorApi';
import Aux from '../../../hoc/_Aux';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { ProveedorUpSert } from './ProveedorUpSert';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
import { asignarEstiloTabla, limpiarEstiloTabla } from '../../../helpers/estiloTabla';
import { ContactoProveedorContainer } from './ContactoProveedorContainer';
const menuId = 29;
const menuIdDepartamento = 9;
const menuIdContactoProveedor = 30;
const menuIdContactoProveedorTelefono = 31;
const menuIdTipoTelefono = 7;
export const ProveedorListar = () => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true)
    const [proveedorId, setProveedorId] = useState(0);
    const [accesos, setAccesos] = useState([]);
    const [abrirModal, setAbrirModal] = useState(false);
    const [abrirModalContacto, setAbrirModalContacto] = useState(false);
    const [catDepartamento, setCatDepartamento] = useState([]);
    const [proveedores, setProveedores] = useState([]);
    const initData = {
        nombre: '',
        descripcion: '',
        departamentoId: 0,
        municipioId: 0,
        direccion: '',
        nit: '',
        email: '',
        estadoId: 1
    };

    const GetDepartamentos = async () => {
        if (accesos.find(acceso => acceso.menuId === menuIdDepartamento && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi('departamento?estadoId=1&include=0');
            if (response) {
                setCatDepartamento(response);
            }
        } else {
            setCatDepartamento([{ departamentoId: '', descripcion: 'No esta autorizado' }]);
        }
        setLoading(false);
    }

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdDepartamento || item.menuId === menuIdContactoProveedor || item.menuId === menuIdContactoProveedorTelefono || item.menuId === menuIdTipoTelefono);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const [dataInicial, setdataInicial] = useState(initData);
    const handleOpenModal = () => {
        setAbrirModal(true);
        setdataInicial(initData);
    }
    const GetProveedores = async () => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            limpiarEstiloTabla("#mytable");
            setLoading(true);
            let response = await callApi('proveedor?estadoId=1;2');
            if (response) {
                setProveedores(response);
            }
            asignarEstiloTabla("#mytable");
        }
        setLoading(false);
    }

    const handleEditar = (id) => {
        let { proveedorId, nombre, descripcion, municipioId, direccion, nit, email, estadoId, Municipio: { departamentoId } } = proveedores.find(item => item.proveedorId === id);
        setdataInicial({
            proveedorId,
            nombre,
            descripcion,
            departamentoId,
            municipioId,
            direccion,
            nit,
            email,
            estadoId
        });
        setAbrirModal(true);
    }
    const handleContactos = (id) => {
        setAbrirModalContacto(true);
        setProveedorId(id);
    }

    const handleDelete = (id) => {
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title: 'Alerta?',
            text: 'Esta seguro que desea eliminar el elemento',
            type: 'warning',
            showCloseButton: true,
            showCancelButton: true
        }).then(async (willDelete) => {
            if (willDelete.value) {
                let method = 'DELETE';
                let response = await callApi(`proveedor/${id}`, {
                    method
                });
                if (response) {
                    alert_exitoso(response);
                    let listActual = proveedores.filter(item => item.proveedorId !== id);
                    setProveedores(listActual);
                }
            } else {
                alert_warning('No se elimin ningn elemento');
            }
        });
    }

    useEffect(() => {
        GetAccesosByMenuId();
    }, [state?.accesos]);

    useEffect(() => {
        GetProveedores();
        GetDepartamentos();
    }, [accesos]);
    return (
        accesos.length > 0 &&
        <Aux>
            <Row className='btn-page'>
                <Col sm={12}>
                    <Card>
                        <Card.Header>
                            <Card.Title as="h5">Proveedores</Card.Title>
                        </Card.Header>
                        <Card.Body>
                            {
                                loading === true ?
                                    <Loading />
                                    : <>
                                        <Row className="align-items-center m-l-0">
                                            <Col />
                                            <Col className="text-right">
                                                {
                                                    accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) &&
                                                    <Button variant="success" className="btn-sm btn-round has-ripple" onClick={handleOpenModal}><i className="feather icon-plus" /> Nuevo Proveedor</Button>
                                                }
                                            </Col>
                                        </Row>
                                        {
                                            accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3) ?
                                                <Table striped hover responsive bordered id="mytable">
                                                    <thead>
                                                        <tr>
                                                            <th>Cdigo</th>
                                                            {
                                                                accesos.find(acceso => (acceso.menuId === menuIdContactoProveedor && acceso.accesoId === 3)) &&
                                                                <th>Contactos</th>
                                                            }
                                                            <th>Nombre</th>
                                                            <th>Descripcion</th>
                                                            <th>Municipio</th>
                                                            <th>Direccion</th>
                                                            <th>Nit</th>
                                                            <th>Correo</th>
                                                            <th>Fecha Creacin</th>
                                                            <th>Estado</th>
                                                            {
                                                                accesos.find(acceso => (acceso.menuId === menuId && acceso.accesoId === 2) || (acceso.menuId === menuId && acceso.accesoId === 4)) &&
                                                                <th></th>
                                                            }
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            proveedores.map(({ proveedorId, nombre, descripcion, direccion, nit, email, fecha_crea, Estado: { descripcion: estado }, Municipio: { descripcion: municipio } }) => {
                                                                return (
                                                                    <tr key={proveedorId}>
                                                                        <td>{proveedorId}</td>
                                                                        {
                                                                            accesos.find(acceso => (acceso.menuId === menuIdContactoProveedor && acceso.accesoId === 3)) &&
                                                                            <td style={{ textAlign: "center" }}><button className="btn-icon btn btn-info btn-sm" onClick={() => { handleContactos(proveedorId) }}><i className="feather icon-users" /></button></td>
                                                                        }
                                                                        <td>{nombre}</td>
                                                                        <td>{descripcion}</td>
                                                                        <td>{municipio}</td>
                                                                        <td>{direccion}</td>
                                                                        <td>{nit}</td>
                                                                        <td>{email}</td>
                                                                        <td>{moment(fecha_crea).format('DD/MM/YYYY')}</td>
                                                                        <td>{estado}</td>

                                                                        {
                                                                            accesos.find(acceso => (acceso.menuId === menuId && acceso.accesoId === 2) || (acceso.menuId === menuId && acceso.accesoId === 4)) &&
                                                                            <td style={{ textAlign: "center" }}>
                                                                                {
                                                                                    accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 2) &&
                                                                                    <button className="btn-icon btn btn-info btn-sm" onClick={() => { handleEditar(proveedorId) }}><i className="feather icon-edit" /></button>
                                                                                }
                                                                                {
                                                                                    accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 4) &&
                                                                                    <button className="btn-icon btn btn-danger btn-sm" onClick={() => { handleDelete(proveedorId) }}><i className="feather icon-trash-2" /></button>
                                                                                }
                                                                            </td>
                                                                        }
                                                                    </tr>
                                                                )
                                                            })
                                                        }
                                                    </tbody>
                                                </Table>
                                                : <NoAutorizado />
                                        }
                                    </>
                            }
                            {
                                abrirModal === true &&
                                <ProveedorUpSert abrirModal={abrirModal} setAbrirModal={setAbrirModal} catDepartamento={catDepartamento} GetProveedores={GetProveedores} dataInicial={dataInicial} />
                            }
                            {
                                abrirModalContacto === true &&
                                <ContactoProveedorContainer abrirModal={abrirModalContacto} setAbrirModal={setAbrirModalContacto} proveedorId={proveedorId} accesos={accesos} />
                            }
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}
