import React, { useState, useEffect } from 'react'
import { Modal } from 'react-bootstrap';
import moment from 'moment';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import Loading from '../base/Loading';
import { ContactoProveedorUpSert } from './ContactoProveedorUpSert';
import { NoAutorizado } from '../base/NoAutorizado';
import { Table } from 'react-bootstrap';
import { TelefonosContactoProveedorListar } from './TelefonosContactoProveedorListar';
import { NoExistenRegistros } from '../base/NoExistenRegistros';
const menuId = 30;
const menuIdContactoProveedorTelefono = 31;
export const ContactoProveedorContainer = ({ abrirModal, setAbrirModal, proveedorId, accesos }) => {
    const [addContacto, setAddContacto] = useState(false);
    const [listContactos, setlistContactos] = useState([]);
    const [loading, setLoading] = useState(false);
    const [infoContacto, setInfoContacto] = useState({});

    const GetlistContactos = async () => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(false);
            let response = await callApi(`contactoproveedor?include=1&estadoId=1;2&proveedorId=${proveedorId}`);
            if (response) {
                setlistContactos(response);
            }
        }
        setLoading(false);
    }

    const handleSetInfoContacto = (id) => {
        let info = listContactos.find(i => i.contacto_proveedorId === id);
        setInfoContacto(info);
    }
    const handleEditarContacto = (id) => {
        let nuevoList = [];
        nuevoList = listContactos.map((item) => {
            if (id === 0) {
                item.edit = false;
                return item;
            }
            if (item.contacto_proveedorId === id) {
                item.edit = true;
                return item;
            } else {
                item.edit = false;
                return item;
            }
        });
        setlistContactos(nuevoList);
    }

    const handleDelete = async (id) => {
        let method = 'DELETE';
        let response = await callApi(`contactoproveedor/${id}`, {
            method
        });
        if (response) {
            alert_exitoso(response);
            let listActual = listContactos.filter(item => item.contacto_proveedorId !== id);
            setlistContactos(listActual);
        }

    }

    useEffect(() => {
        GetlistContactos();
    }, []);
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">{infoContacto?.contacto_proveedorId > 0 ? <button className="btn btn-success btn-sm" type="button" onClick={() => setInfoContacto({})}>Regresar a listado de contactos</button> : 'Listado de Contactos'}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                infoContacto?.contacto_proveedorId > 0 ?
                                    <TelefonosContactoProveedorListar accesos={accesos} {...infoContacto} />
                                    :
                                    accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3) ?
                                        <>
                                            {
                                                (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) && !addContacto) &&
                                                <button className="btn btn-info btn-sm" onClick={setAddContacto}>Nuevo Contacto</button>
                                            }

                                            {
                                                addContacto && <>
                                                    <h4>Informacin de contacto</h4>
                                                    <ContactoProveedorUpSert proveedorId={proveedorId} contacto_proveedorId={0} nombre="" apellido="" estadoId={1} GetlistContactos={GetlistContactos} setAddContacto={setAddContacto} />
                                                </>
                                            }
                                            {
                                                listContactos.length <= 0 ?
                                                    <NoExistenRegistros />
                                                    : <>
                                                        <hr></hr> <Table striped hover responsive bordered id="tableContactos">
                                                            <thead>
                                                                <tr>
                                                                    <th>Cdigo</th>
                                                                    {
                                                                        accesos.find(acceso => (acceso.menuId === menuIdContactoProveedorTelefono && acceso.accesoId === 3)) &&
                                                                        <th style={{ textAlign: "center" }}>Tels</th>
                                                                    }
                                                                    <th>Nombre</th>
                                                                    <th>Fecha Creacin</th>
                                                                    <th>Estado</th>

                                                                    {
                                                                        accesos.find(acceso => ((acceso.menuId === menuId && acceso.accesoId === 2)) || (acceso.menuId === menuId && acceso.accesoId === 4)) &&
                                                                        <th></th>
                                                                    }
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {
                                                                    listContactos.map((item) => {
                                                                        let { contacto_proveedorId, nombre, apellido, fecha_crea, edit = false, Estado: { descripcion: descEstado } } = item;
                                                                        let nombreContacto = `${nombre} ${apellido}`;
                                                                        return <tr key={contacto_proveedorId}>
                                                                            <td>{contacto_proveedorId}</td>
                                                                            {
                                                                                edit &&
                                                                                <td colSpan="5">
                                                                                    <ContactoProveedorUpSert {...item} GetlistContactos={GetlistContactos} handleEditarContacto={handleEditarContacto} />
                                                                                </td>
                                                                            }
                                                                            {
                                                                                !edit &&
                                                                                <>
                                                                                    {
                                                                                        accesos.find(acceso => (acceso.menuId === menuIdContactoProveedorTelefono && acceso.accesoId === 3)) &&
                                                                                        <td style={{ textAlign: "center" }}><button className="btn-icon btn btn-info btn-sm" onClick={() => handleSetInfoContacto(contacto_proveedorId)}><i className="feather icon-phone" /></button>&nbsp;</td>
                                                                                    }
                                                                                    <td>{nombreContacto}</td>
                                                                                    <td>{moment(fecha_crea).format('DD/MM/YYYY')}</td>
                                                                                    <td>{descEstado}</td>

                                                                                    {
                                                                                        accesos.find(acceso => ((acceso.menuId === menuId && acceso.accesoId === 2)) || (acceso.menuId === menuId && acceso.accesoId === 4)) &&
                                                                                        <td style={{ textAlign: "center" }}>

                                                                                            {
                                                                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 2) &&
                                                                                                <button className="btn-icon btn btn-info btn-sm" onClick={() => handleEditarContacto(contacto_proveedorId)}><i className="feather icon-edit" /></button>
                                                                                            }
                                                                            &nbsp;
                                                                            {
                                                                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 4) &&
                                                                                                <button className="btn-icon btn btn-danger btn-sm" onClick={() => handleDelete(contacto_proveedorId)}><i className="feather icon-trash-2" /></button>
                                                                                            }
                                                                                        </td>
                                                                                    }
                                                                                </>
                                                                            }
                                                                        </tr>
                                                                    })
                                                                }
                                                            </tbody>
                                                        </Table>
                                                    </>
                                            }
                                        </>
                                        : <NoAutorizado />
                            }
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
