import { Alert } from 'bootstrap';
import React, { useState, useEffect } from 'react'
import moment from 'moment';
import { Table } from 'react-bootstrap';
import callApi from '../../../helpers/conectorApi';
import { Row } from 'react-bootstrap';
import { Col } from 'react-bootstrap';
import { NoExistenRegistros } from '../base/NoExistenRegistros';
import Loading from '../base/Loading';
import { TelefonoContactoProveedorUpSert } from './TelefonoContactoProveedorUpSert';
import { alert_exitoso } from '../../../helpers/Notificacion';
const menuId = 31;
const menuIdTipoTelefono = 7;
export const TelefonosContactoProveedorListar = ({ contacto_proveedorId, nombre, apellido, accesos = [] }) => {
    const [telefonos, setTelefonos] = useState([]);
    const [loading, setLoading] = useState(false);
    const [addTelefono, setAddTelefono] = useState(false);
    const [CatTIpoTelefono, setCatTIpoTelefono] = useState([]);
    const [colsPan,setColspan]=useState(5);

    const GetTelefonoContacto = async () => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi(`telefonocontactoproveedor?contacto_proveedorId=${contacto_proveedorId}&estadoId=1;2`);
            if (response) {
                setTelefonos(response);
            }
        }
        setLoading(false);
    }

    const GetTipoTelefono = async () => {
        if (accesos.find(acceso => acceso.menuId === menuIdTipoTelefono && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi(`tipotelefono?include=0;estadoId=1`);
            if (response) {
                setCatTIpoTelefono(response);
            }
        }
        setLoading(false);
    }

    const handleEditarTelefono = (id) => {
        let nuevoList = [];
        nuevoList = telefonos.map((item) => {
            if (id === 0) {
                item.edit = false;
                return item;
            }
            if (item.telefono_contacto_proveedorId === id) {
                item.edit = true;
                return item;
            } else {
                item.edit = false;
                return item;
            }
        });
        setTelefonos(nuevoList);
    }
    const handleEliminarTelefono = async (id) => {
        let method = 'DELETE';
        let response = await callApi(`telefonocontactoproveedor/${id}`, {
            method
        });
        if (response) {
            alert_exitoso(response);
            let listActual = telefonos.filter(item => item.telefono_contacto_proveedorId !== id);
            setTelefonos(listActual);
        }
    }
    useEffect(() => {
        GetTelefonoContacto();
        GetTipoTelefono();
    }, []);

    return (
        <>
            {
                loading === true ?
                    <Loading />
                    : <>
                        <h4>Telfonos del Contacto {nombre} {apellido}</h4>
                        <hr></hr>
                        {
                            (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) && !addTelefono) &&
                            <button className="btn btn-info btn-sm" onClick={() => setAddTelefono(1)}>Nuevo Telfono</button>
                        }
                        {
                            addTelefono &&
                            <>
                                <h4>Nuevo</h4>
                                <TelefonoContactoProveedorUpSert contacto_proveedorId={contacto_proveedorId} telefono_contacto_proveedorId={0} telefono="" tipo_telefonoId={1} estadoId={1} GetTelefonoContacto={GetTelefonoContacto} setAddTelefono={setAddTelefono} CatTIpoTelefono={CatTIpoTelefono} />
                            </>
                        }
                        {
                            telefonos.length <= 0 ?
                                <NoExistenRegistros />
                                : <>
                                    <hr></hr>
                                    <Table striped hover responsive bordered id="tableTelefonos">
                                        <thead>
                                            <tr>
                                                <th>Cdigo</th>
                                                <th>Telefono</th>
                                                <th>Tipo</th>
                                                <th>Fecha Creacin</th>
                                                <th>Estado</th>
                                                {
                                                    accesos.find(acceso => ((acceso.menuId === menuId && acceso.accesoId === 2)) || (acceso.menuId === menuId && acceso.accesoId === 4)) &&
                                                    <th></th>
                                                }
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                telefonos.map((item) => {
                                                    let { telefono_contacto_proveedorId, tipo_telefonoId, telefono, Estado: { descripcion: descEstado }, fecha_crea, TipoTelefono: { descripcion: descTipoTel }, edit = false } = item;
                                                    return (
                                                        <tr key={telefono_contacto_proveedorId}>
                                                            <td>{telefono_contacto_proveedorId}</td>
                                                            {
                                                                edit &&
                                                                <td colSpan="5">
                                                                    <TelefonoContactoProveedorUpSert {...item} GetTelefonoContacto={GetTelefonoContacto} handleEditarTelefono={handleEditarTelefono} setAddTelefono={setAddTelefono} CatTIpoTelefono={CatTIpoTelefono} />
                                                                </td>
                                                            }
                                                            {
                                                                !edit && <>
                                                                    <td>{telefono}</td>
                                                                    <td>{descTipoTel}</td>
                                                                    <td>{moment(fecha_crea).format('DD/MM/YYYY')}</td>
                                                                    <td>{descEstado}</td>
                                                                    {
                                                                        accesos.find(acceso => ((acceso.menuId === menuId && acceso.accesoId === 2)) || (acceso.menuId === menuId && acceso.accesoId === 4)) &&
                                                                        <td style={{ textAlign: "center" }}>
                                                                            {
                                                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 2) &&
                                                                                <button className="btn-icon btn btn-info btn-sm" onClick={() => handleEditarTelefono(telefono_contacto_proveedorId)}><i className="feather icon-edit" /></button>
                                                                            }
                                                                            &nbsp;
                                                                            {
                                                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 4) &&
                                                                                <button className="btn-icon btn btn-danger btn-sm" onClick={() => handleEliminarTelefono(telefono_contacto_proveedorId)}><i className="feather icon-trash-2" /></button>
                                                                            }
                                                                        </td>
                                                                    }
                                                                </>
                                                            }
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </Table>
                                </>
                        }
                    </>
            }
        </>
    )
}
