import React from 'react'
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';

export const TelefonoContactoProveedorUpSert = ({ CatTIpoTelefono, GetTelefonoContacto, telefono_contacto_proveedorId, telefono, estadoId, setAddTelefono, handleEditarTelefono, contacto_proveedorId, tipo_telefonoId }) => {
    const [values, handleOnChange] = useForm({ telefono, estadoId, contacto_proveedorId, tipo_telefonoId });
    const ActualizarRegistro = async () => {
        let response = await callApi(`telefonocontactoproveedor/${telefono_contacto_proveedorId}`, {
            method: 'PUT',
            body: JSON.stringify(values)
        });

        if (response) {
            alert_exitoso(response);
        }
        GetTelefonoContacto();
    }
    const Nuevo = async () => {
        let response = await callApi(`telefonocontactoproveedor`, {
            method: 'POST',
            body: JSON.stringify(values)
        });

        if (response) {
            alert_exitoso("Contacto de proveedor registrado exitosamente");
            setAddTelefono(false);
        }
        GetTelefonoContacto();
    }
    const handleSubtmit = () => {
        if (telefono_contacto_proveedorId > 0) {
            ActualizarRegistro();
        } else {
            Nuevo();
        }
    }
    const handleCancelar = () => {
        if (telefono_contacto_proveedorId > 0) {
            handleEditarTelefono(0);
        } else {
            setAddTelefono(false);
        }
    }

    return (
        <>
            <input type="text" name="telefono" required className="form-control" placeholder="Nmero de telfono" value={values.telefono} onChange={handleOnChange} />
            <select className="form-control" name="tipo_telefonoId" onChange={handleOnChange} value={values.tipo_telefonoId}>
                {
                    CatTIpoTelefono.map(({ tipo_telefonoId, descripcion }) => {
                        return <option key={tipo_telefonoId} value={tipo_telefonoId}>{descripcion}</option>
                    })
                }
            </select>
            {
                telefono_contacto_proveedorId > 0 &&
                <select className="form-control" name="estadoId" onChange={handleOnChange} value={values.estadoId}>
                    <option value="1">Activo</option>
                    <option value="2">Inactivo</option>
                </select>
            }

            <br></br>
            <button className="btn btn-success btn-sm mr-2" type="button" onClick={handleSubtmit}>{telefono_contacto_proveedorId > 0 ? 'Actualizar' : 'Nuevo'}</button>
            <button className="btn btn-danger btn-sm" type="button" onClick={handleCancelar}>Cancelar</button>
        </>

    )
}
