import React from 'react'
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';

export const ContactoProveedorUpSert = ({ GetlistContactos, contacto_proveedorId, nombre, apellido, estadoId, setAddContacto, handleEditarContacto, proveedorId }) => {
    const [values, handleOnChange, , setValues] = useForm({ nombre, apellido, estadoId, proveedorId });
    const ActualizarRegistro = async () => {
        let response = await callApi(`contactoproveedor/${contacto_proveedorId}`, {
            method: 'PUT',
            body: JSON.stringify(values)
        });

        if (response) {
            alert_exitoso(response);
        }
        GetlistContactos();
    }
    const Nuevo = async () => {
        let response = await callApi(`contactoproveedor`, {
            method: 'POST',
            body: JSON.stringify(values)
        });

        if (response) {
            alert_exitoso("Contacto de proveedor registrado exitosamente");
            setAddContacto(false);
        }
        GetlistContactos();
    }
    const handleSubtmit = () => {
        if (contacto_proveedorId > 0) {
            ActualizarRegistro();
        } else {
            Nuevo();
        }
    }
    const handleCancelar = () => {
        if (contacto_proveedorId > 0) {
            handleEditarContacto(0);
        } else {
            setAddContacto(false);
        }
    }

    return (
        <>
            <input type="text" name="nombre" required className="form-control" placeholder="Nombre" value={values.nombre} onChange={handleOnChange}>
            </input><input type="text" name="apellido" required className="form-control" placeholder="Apellido" value={values.apellido} onChange={handleOnChange}></input>
            {
                contacto_proveedorId > 0 &&
                <select className="form-control" name="estadoId" onChange={handleOnChange} value={values.estadoId}>
                    <option value="1">Activo</option>
                    <option value="2">Inactivo</option>
                </select>
            }

            <br></br>
            <button className="btn btn-success btn-sm mr-2" type="button" onClick={handleSubtmit}>{contacto_proveedorId > 0 ? 'Actualizar' : 'Nuevo'}</button>
            <button className="btn btn-danger btn-sm" type="button" onClick={handleCancelar}>Cancelar</button>
        </>

    )
}
